
<template>
  <div>
    <a-modal
      @cancel="handleCancel"
      @ok="handleCreate"
      :visible="visible"
      :confirmLoading="confirmLoading"
      title=""
      okText=""
    >
      <a-form :form="form">
        <a-form-item :labelCol="{ span: 8 }" :wrapperCol="{ span: 14 }" label="">
          <a-input
            type="password"
            v-decorator="[
              'oldPwd',
              {
                initialValue: currRecord.name,
                rules: [
                  { required: true, message: '' },
                ]
              }
            ]"
            placeholder=""
          />
        </a-form-item>
        <a-form-item :labelCol="{ span: 8 }" :wrapperCol="{ span: 14 }" label="">
          <a-input
            type="password"
            v-decorator="[
              'newPwd',
              {
                initialValue: currRecord.name,
                rules: [
                  { required: true, message: '' },
                  { min: 6, message: ' 6 ' },
                  { max: 32, message: ' 32 ' },
                  { validator: validatePwd },
                ]
              }
            ]"
            placeholder=""
          />
        </a-form-item>
        <a-form-item :labelCol="{ span: 8 }" :wrapperCol="{ span: 14 }" label="">
          <a-input
            type="password"
            v-decorator="[
              'password2',
              {
                initialValue: currRecord.name,
                rules: [
                  { required: true, message: '' },
                ]
              }
            ]"
            placeholder=""
          />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>
<script>
import { baseModalMixins, formModalMixins } from "@/mixins/modalMixin";
import rulesObj from '@/utils/rules'
import { modifyPwd as fetchAPI  } from "@/api/user/user";
export default {
  mixins: [baseModalMixins, formModalMixins],
  data() {
    return {
      fetchAPI,
      rulesObj,
      name: "editPwd"
    };
  },

  methods: {
    validatePwd (rule, value, callback) {
      const regsGroup = [/[a-zA-Z]/, /[0-9]/, /[_\-!@#]/];
      const ret = regsGroup.filter(reg => reg.test(value));
      ret.length < 2 ? callback('(! - _ # @)2') : callback();
    },
    onShow () {
      this.formValues = { hardDiskId: this.currRecord.id }
    },
    handleCreate() {
      const self = this;
      this.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          if (values.password2 === values.newPwd) {
            Object.assign(self.formValues, values);
            self.handleFetch();
          } else {
            this.form.setFields({
              password2: {
                value: values.password2,
                errors: [
                  {
                    message: "!"
                  }
                ]
              }
            });
          }
        }
      });
    },
  }
};
</script>
