<template>
  <div>
    <page-layout>
      <a-spin :spinning="loading" size="large" />
      <a-list v-if="!loading" itemLayout="horizontal" :dataSource="listData">
        <a-list-item slot="renderItem" slot-scope="item">
          <a-list-item-meta
            :description="item.desc"
          >
            <a slot="title" >{{item.title}}</a>

          </a-list-item-meta>
            <a slot="actions" @click="handleClick(item)">{{ item.btnText }}</a>
        </a-list-item>
      </a-list>
    </page-layout>

    <bind-email />
    <edit-phone-or-email :currItem="currItem"/>
    <edit-pwd />
  </div>
</template>

<script>
import BindEmail from './Modal/BindEmail'
import EditPhoneOrEmail from './Modal/EditPhoneOrEmail'
import EditPwd from './Modal/EditPwd'
import PageLayout from "@/components/Layout/PageLayout";
export default {
  components: {
    PageLayout,
    BindEmail,
    EditPhoneOrEmail,
    EditPwd,
  },
  data() {
    return {

      module: "user",
      id: "security",
      name: "",

      loading: false,
      userInfo: {},

      listData: [],
      currItem: {}
    };
  },
  created () {
    this.fetch()
  },
  methods: {
    handleRefresh () {
      this.fetch()
    },
    async fetch () {
      try {
        this.loading = true;
        const resp = await this.$store.dispatch('app/fetchUserInfo')
        this.$store.commit('app/setUserInfo', resp);
        this.userInfo = resp;
        this.listData = [
          {
            id: 'editPwd',
            key: 'editPwd',
            title: "",
            desc: "",
            btnText: '',
          },
          {
            id: 'editPhone',
            key: 'phone',
            title: "",
            desc: ` : ${resp.phone}`,
            btnText: '',
          },
          {
            id: resp.email ? 'editPhone' : 'editEmail',
            key: 'email',
            title: "",
            desc: resp.email ? ` : ${resp.email}` : "",
            btnText: resp.email ? '' : '',
          },
        ];
        this.loading = false;
      } catch (error) {
        this.loading = false;
        
      }
    },
    handleShowModal (key) {
      this.$store.commit(`${this.id}/toggleModalVisible`, key);
    },
    handleClick (item) {
      // console.log(item);
      this.currItem = item;
      this.handleShowModal (item.id);
    }
  }
};
</script>

